import * as React from "react"

import Layout from "../components/layout"
import Seo from "../components/seo"
import "./get-involved.css"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"

const get_involved = () => (
    <Layout page="get_involved">
      <Seo title="Get Involved" />
      <section className="get-involved-title">
        <StaticImage src="../images/get-involved-title.png" alt="Welcome To The Funnest Part!" className="get-involved-titleImage" />
      </section>
      <section className="get-involved-01">
        <div className="get-involved-widthLimit">
          <div className="columns">
            <div className="column is-one-third has-text-centered">
              <strong className="get-involved-01-text">01</strong>
            </div>
            <div className="column has-text-centered">
              <Link to="donate/" className="get-involved-01-donate-to-us">DONATE TO US</Link>
              <p className="get-involved-01-description get-involved-description">
                As a startup non-profit organization, we need your kindness to make a difference. With your donation, we can serve the community with enriched projects and events.
              </p>
            </div>
          </div>
        </div>
      </section>
      <section className="get-involved-02">
        <div className="get-involved-widthLimit">
          <div className="columns">
            <div className="column is-one-third has-text-centered">
              <strong className="get-involved-02-text">02</strong>
            </div>
            <div className="column has-text-centered">
              <Link to="volunteer/" className="get-involved-02-be-our-partner">BE OUR PARTNERS</Link>
              <p className="get-involved-02-description get-involved-description">
                Become a part of what we do! With more than 10 years of volunteer experience, we have great reputations among our partners and are able to provide professional volunteers to help with your events
              </p>
              <p className="get-involved-02-description2 get-involved-description">
                Please get in touch with us for any volunteer opportunities, and leadership programs.
              </p>
            </div>
          </div>
        </div>
      </section>
      <section className="get-involved-03">
        <div className="get-involved-widthLimit">
          <div className="columns">
            <div className="column is-one-third has-text-centered">
              <strong className="get-involved-03-text">03</strong>
            </div>
            <div className="column has-text-centered">
              <Link to="join-us/" className="get-involved-03-donate-to-us">JOIN US</Link>
              <p className="get-involved-03-description get-involved-description">
                Become a member of CIS or help expand CIS to your high school. Gain leadership training and happiness with the big family!
                <a href="https://forms.gle/TJeYFhi4fttC4rUbA" className="get-involved-03-form-link">
                  CLICK
                </a>
              </p>
            </div>
          </div>
        </div>
      </section>
    </Layout>
)

export default get_involved
